import React from 'react'
import Carditem from './Carditem'
import './Cards.css'

function Cards() {
    return (
        <div className='cards'>
            <h1>Check out these EPIC Destination!</h1>
            <div className="cards__wrapper">
                <ul className="cards__items">
                    <Carditem
                    src={process.env.PUBLIC_URL + '/images/img-9.jpg'}
                    text='Explore the hidden waterfall deep inside the Amazon Jungle'
                    label='Adventure'
                    path='/services'
                    />
                    
                    <Carditem
                    src={process.env.PUBLIC_URL + '/images/img-2.jpg'}
                    text='Travel through the Islands of Bali in a Private Cruise'
                    label='Tourism'
                    path='/services'
                    />

                    <Carditem
                    src={process.env.PUBLIC_URL + '/images/img-5.jpg'}
                    text='Check-in for most laxuriuos experience Ever !'
                    label='Hotel'
                    path='/services'
                    />
                </ul>
                <ul className="cards__items">
                    <Carditem
                    src={process.env.PUBLIC_URL + '/images/img-3.jpg'}
                    text='Enjoy the deep silence of the See'
                    label='Cruise'
                    path='/services'
                    />
                    
                    <Carditem
                    src={process.env.PUBLIC_URL + '/images/img-4.jpg'}
                    text='Enjoy the footbal match in the island'
                    label='Sports'
                    path='/services'
                    />
                </ul>
                <ul className="cards__items">
                    <Carditem
                    src={process.env.PUBLIC_URL + '/images/img-1.jpg'}
                    text='Explore the hidden Secret inside the Valleys of the mountain'
                    label='Explorer'
                    path='/services'
                    />
                </ul>
            </div>
        </div>
    )
}

export default Cards;
