import React from 'react'
import { Button } from './Button'
// const logo = require('../../public/images/img-home.jpg'); // with require
import '../App.css'
import './HeroSection.css'
function HeroSection() {
const myStyle= {
    background: `url(${process.env.PUBLIC_URL + '/images/img-home.jpg'}) center center/cover no-repeat`
}

    return (
        <div style={myStyle} className='hero-container'>
            {/* <video src="/videos/video-1.mp4" autoPlay muted loop/> */}
            <h1>ADVENTURE AWAITS</h1>
            <p>What are you waiting for?</p>
            <div className="hero-btns">
                <Button className='btns' buttonStyle='btn--outline' buttonSize='btn--large'>GET STARTED</Button>

                <Button className='btns' buttonStyle='btn--primary' buttonSize='btn--large'>
                    WATCH TRAILER 
                    <i className="far fa-play-circle"/>
                </Button>
            </div>
        </div>
    )
}

export default HeroSection
